import React, { Fragment} from 'react';
import '../Hero.css';
import avatar from '../avatar.svg';
import heroFooter from '../desk.png';
const Hero = () => {
    return ( 
        <Fragment>
            <section className="hero-section">
                <div className="hero-tittle">
                    <h1>Front-end Developer</h1>
                    <h2>I code beautifully simple things, and I love what I do.</h2>
                    <img src={avatar} alt="Anuar abutook avatar img"/>
                </div>
                <div className="hero-footer">
                    <img src={heroFooter} alt="A desk with a computer"/>
                </div>
            </section>
        </Fragment>
     );
}
 
export default Hero;