import React from 'react';
import { BrowserRouter, Switch , Route } from 'react-router-dom';
import '../index.css';
import Contact from './Contact.jsx';
import Content from './Content.jsx';
import Layout from './Layout.jsx';

function App (){
    return(
        <BrowserRouter>
            <Layout>
                <Switch>
                    <Route exact path="/">
                        <Content />
                    </Route>
                    <Route exact path="/contact">
                        <Contact/>
                    </Route>
                </Switch>
            </Layout>
        </BrowserRouter>
    );
}

export default App;