import React from 'react';
import '../Nav.css';
import logo from '../logo-blue.png';
import { Link } from 'react-router-dom';
const Navbar = (props) => {
    return (
        <nav className="navbar" id="navbar">
            <div className="navbar-content">
                <div className="navbar-home-img">
                    <Link to="/" onClick={()=>{props.editButtonHome()}}>
                        <img src={logo} alt="Anuar Abutook Front-end Developer" className="logo"/>
                    </Link>
                </div>
                <div className="navbar-say-hello">
                    <a href="https://www.linkedin.com/in/anuarabutook/" target="_blank" rel="noreferrer" className="linkedin">
                        Linkedin
                    </a>
                    {   props.contact ?
                        (
                            <Link to="/" className="say-hello" onClick={()=>{props.editButtonHome()}} >
                                X
                            </Link>
                        ):(
                            <Link to="/contact" className="say-hello" onClick={()=>{props.editButton()}}>
                                Say Hello
                            </Link>
                        )
                    }
                </div>
            </div>
        </nav>
    );
}
 
export default Navbar;