import React from 'react';
import '../Contact.css';
import avatar from '../avatar.svg';
import emailjs from 'emailjs-com';

const Contact = () => {
    function sendEmail(e){
        e.preventDefault();
        
        emailjs.sendForm('service_qfoo83u', 'template_6xufc5m', e.target, 'user_VskqLg4mLPYcNOfawIvVr')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
        e.target.reset();
    }
    return ( 
        <>
            <div className="container">
                <section className="hero-container">
                    <div className="hero-image">
                        <img src={avatar} alt="dev-avatar"/>
                    </div>
                    <div className="tittle-container">
                        <h1>Thanks for taking the time to reach out.</h1>
                        <h1>How can I help you today?</h1>
                    </div>
                </section>
                <section className="form-section">
                    <form className="form" onSubmit={sendEmail}>
                        <div className="form-input">
                            <div className="form-input-item">
                                <label htmlFor="Name">Name</label>
                                <input type="text" name="name" required/>
                            </div>
                            <div className="form-input-item input-email">
                                <label htmlFor="Email">Email</label>
                                <input type="email" name="email" required/>
                            </div>
                        </div>
                        <div className="textarea">
                            <label htmlFor="Message">Message</label>
                            <textarea name="message" rows="5" required/>
                        </div>
                        <div className="btn-div">
                            <button type="submit" className="btn">Submit</button>
                        </div>
                    </form>
                </section>
            </div>
        </>
     );
}
 
export default Contact;