import React, { useState } from 'react';
import Nav from './Nav.jsx';

const Layout = ({children,...restProps}) => {
    const [contact, setContact] = useState(false);
    const editButton = () =>{
        setContact(true);
    }
    const editButtonHome = () =>{
        setContact(false);
    }
    return ( 
        <>
            <Nav contact={contact} editButton={editButton} editButtonHome={editButtonHome}/>
            {children}
        </>
     );
}
 
export default Layout;